.footer {
  padding: 55px 0 25px;
  background-color: #fff5f6;
}
.footer__suptitle {
  margin-bottom: 17px;
  font-family: var(--secondaryFont);
  font-weight: 400;
  font-size: 18px;
  line-height: 1.16;
  text-transform: uppercase;

  color: var(--informationColor);
}
.footer__title {
  margin-bottom: 43px;
  width: 200px;

  font-weight: 400;
  font-size: 34px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}
.footer__list {
  margin-bottom: 46px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.footer__item {
  margin: 0px 12px 0px 12px;
  transition: background-color 250ms cubic-bezier(0.075, 0.82, 0.165, 1);
  display: flex;
  align-items: center;
  justify-content: center;

  background-color: #fea5bb;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  cursor: pointer;

  &:nth-child(1) {
    margin-left: 0;
  }
  &:nth-child(3n) {
    margin-right: 5px;
  }
  &:hover,
  &:focus {
    background-color: var(--accentColor);
  }
}
.footer__link {
  display: flex;
  align-items: center;
  justify-content: center;

  color: var(--primaryTextColor);
}
.footer__icon {
  fill: currentColor;
}
.footer__contact-text {
  margin-bottom: 43px;

  font-weight: 700;
  font-size: 12px;
  line-height: 1.86;

  color: var(--secondaryTextColor);
}
.footer__phone {
  text-decoration: none;
  color: var(--accentColor);
}
.footer__copyright {
  margin: auto;
  width: 150px;

  font-weight: 700;
  font-size: 14px;
  line-height: 1.86;
  letter-spacing: 0.02em;

  color: var(--descriptionColor);
  text-align: center;
}
.footer__underline {
  &::before {
    margin-bottom: 22px;
    content: '';
    display: block;
    border: 1px solid #e1e1e1;
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .footer__contact-text {
    text-align: right;
  }
}
@media screen and (min-width: 768px) {
  .footer {
    padding-top: 80px;
    &-title__wrapper {
      display: flex;
      align-items: baseline;
      justify-content: space-between;
    }
  }
  .footer__suptitle {
    width: 340px;
    font-size: 26px;
    line-height: 1.15;
    letter-spacing: 0.04em;
  }
  .footer__title {
    margin-bottom: 91px;
    width: 340px;
    font-size: 48px;
    line-height: 1.14;
    letter-spacing: 0.06em;
  }
  .footer__contact-text {
    font-size: 14px;
    margin-bottom: 0px;
  }
  .footer__copyright {
    width: 276px;
  }
}
@media screen and (min-width: 1200px) {
  .footer {
    padding-bottom: 10px;
  }
  .footer__underline {
    &::before {
      margin-bottom: 9px;
    }
  }
  .footer__suptitle {
    font-size: 30px;
    line-height: 1.13;
    width: 100%;
  }
  .footer__title {
    margin-bottom: 97px;
    width: 100%;
    font-size: 58px;
    line-height: 1.13;
  }
  .footer__contact-text {
    font-size: 16px;
  }
  .footer__list {
    margin-bottom: 50px;
  }
}
